<template>
    <div class="conteinerName" v-for="(name, index) in names" :key="index">
        <div class="number">
            <span>{{index + 1}}</span>
        </div>
        <div class="init">
            <p class="init-name">{{ nameCl(index) }}</p>
            <p class="init-time" v-if="iventStart"> {{ iventTime() }}</p> 
            <p class="init-claim" v-if="iventClaim"> 
              <span>Activiti Type:</span> {{iventClaim}}
            </p>
        </div>
    </div>
  </template>
  
  <script>
  export default {
    props: {
      names: {
        type: Object
      },
      iventStart: {
        type: Date
      },
      iventStop: {
        type: Date
      },
      iventClaim: {
        type: String
      }
    },
   
    methods: {
      nameCl(index) {
        return (this.names[index].fistName + " " + this.names[index].secondName)
      },

      iventTime() {
        return (this.iventStart + " - " + this.iventStop + " (" + this.iventStop - this.iventStart + ")" )
      }
    }, 
    watch: {
      names: {
        handler(newValue) {
          console.log(newValue)
        },
        deep:true
      
      } 
    }
    
  }
  </script>
    
  <style scoped>
  p {
    margin: 0 0 0.3rem 0;
  }  

  .conteinerName {
    padding: 0.5rem 0.1rem;
    display: flex;
    align-items: flex-start;
    border-bottom: 1px solid #e2e8f0;
  }
  .number {
    margin-right: 1rem;
    width: 1.5rem;
    height: 1.5rem;
    background: #008000;
    border-radius: 100%;
    color: #fff;
    text-align: center;
    font-size: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .init {
    font: normal Roboto;
    color: rgba(88,86,86,.847);
    letter-spacing: 0;
  }

  .init-name {
    font-weight: 700;
    font-size: 1rem;
  }

  .init-time {
    font-weight: 400;
    font-size: 0.8rem;
  }

  .init-claim {
    font-weight: 400;
    font-size: 0.8rem;
  }

  .init-claim > span {
    font-weight: 700;
  }

  </style>
  